body{
    //background-color:#eceff1;
    background-color: #eee;
    color:#555;
}

a{
    color:#4b6dc6;
    text-decoration:none ;
    &:hover{
        text-decoration: none;
        color:#809bf9;
    }
    &:not([href]),&:not([href]):hover{
        color:#666;
        text-decoration: none;
    }
}
nav.navbar{
    width:100%;
    position:fixed;
    top:0;
    z-index:100;

    &.navbar-scroll{
        border-bottom: 1px solid #004395;
        box-shadow:0 0 5px 4px rgba(0, 0, 0, 0.3);
        .navbar-brand,.nav-link{
            color:#4b6dc6;
        }
        .nav-link:hover,.nav-item.active .nav-link{
            color:#545b62 !important;
        }
        
    }

}

footer{
    //text-align: center;
    padding:1rem 0;
    font-size: 80%;
    color: #6c757d;
    span{
        padding-right:0.5rem;
    }
}




.main-container{
    position: relative;
    //background-color:white;
    top:25rem;
    
}

.img-container{
    position: absolute;
    left:0;
    right:0;
    padding:0;
    //z-index:-1;
    .carousel {
        height:30rem;    
       
        top:0;
        //z-index: -1;
        left:0;
        //padding:0 15px;
        .carousel-inner {
            height:100%;
            .carousel-item{
                height:100%;
                background-size:cover;
                background-position: center;
                background-repeat: no-repeat;
                z-index:-1;
            }
        }
    }

}

.main-shade{
    margin:0 -15px;
    
    .main-shade-svg{
       
        height:5rem;
     
        .main-shade-path{
            fill:white
        }
    }
}

.home{
    
    padding:2rem;
    background-color: white;
    justify-content: space-between;
    flex-wrap: wrap-reverse;
    
    .info-container{
       
        h3,h4,h5{
            color:#4b6dc6;
        }
    }
    .paper-container{
        padding:0;
        border:none;
    }
    .project-container{
        border:none;
        margin-top:2rem;
        padding:0;
        ul{
            list-style-type: none;
            padding:0;
            li{
                border-top:1px solid #aaa;
            }
        }
    }
}

.spinner-box{
    padding-top:5rem;
    padding-bottom: 3rem;
    .spinner-border{
        height:3rem;
        width:3rem;
    }
}

.project-container,.paper-container,.user-container,.patent-container{
    padding-top:5rem;
    padding-left:2rem;
    padding-right:2rem;
    padding-bottom: 2rem;
    background-color: white;
    border: 1px solid #dee2e6;
    .title,h3{
        padding-bottom: 1rem;
        color:#4b6dc6;
        margin-bottom: 1rem;
    }
    h4,h5{
        color:#4b6dc6;
        //text-align: center;
    }
    h4.subtitle,h5.subtitle{
        text-align:center;
    }
}
.project-container{
    //border:2px solid black;
    
   ul{
       list-style-type: circle;
   }
    .card{
        //background-color: #f4f1f1;
        //box-shadow: 0 0 3px 2px rgba(0,0,0,0.1);
        min-width:10rem;
        margin-bottom: 15px;
        
    }
    .badge{
        white-space: normal;
        margin-right:1rem;
    }
    /*span.badge{
        color: #fff;
        background-color: #4b6dc6;
        //line-height: 1.5;
    }*/
   /* .card-text{
        text-overflow: ellipsis;
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
    }*/
    
}
.paper-container{
    
    /*h4{
        padding-left:1rem;
    }
    .title{
        padding:5rem 0 1rem 1rem;
    }*/
    .paper-item{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .paper-item-title{
            word-break:break-all;
        }
    }
    .paper-item-link{
        flex-basis:8rem;
        text-align:center;
    }
    .list-group{
        padding:2rem 0;
    }

    .card{
        //background-color: #f4f1f1;
       
        box-shadow: 0 0 3px 2px rgba(0,0,0,0.1);
        min-width:15rem;
        margin-bottom: 15px;
        span.badge{
            color: #fff;
            background-color: #4b6dc6;
        }
    }
}

.user-container{
    
    /*.title{
        padding:0 0 1rem 1rem;
    }
    h4{
        padding-left:1rem;
    }*/
    h4,h5{
        padding-bottom: 1rem;
    }
    .card-deck{
        justify-content: space-around;
    }
}
.user-item.card{
    width:12rem;
    flex:0 1 auto;
    margin-bottom: 15px;
    align-items: center;
    background-color: transparent;
    border:none;
    .card-img-top{
        width:8rem;
        height:8rem;
        //border:1px solid rgba(0,0,0,.125);
        box-shadow:0 0 3px 2px rgba(0,0,0,0.1);
        transition:box-shadow .5s ease-in-out,
                   border-radius .5s ease-in-out;
        &:hover{
            box-shadow:0 0 5px 4px rgba(0,0,0,0.2);
            border-radius: 10% !important;
        }
    }
}

.user-single{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    min-height: 35rem;
    .card-body{
        text-align: center;
    }
    dd{
        max-width: 35rem;
    }
}


.patent-container{
    ul{
        list-style-type: circle;
    }
    li{
        display: list-item;
    }
    .title{
        color:#4b6dc6;
        
    }
    span{
        margin-right:15px;
    }
    
}
.bg-grey{
    background-color:#eceff1;
}

@keyframes placeHolderShimmer{
    0%{
        background-position: -8rem
    }
    100%{
        background-position: 0
    }
}
.content-loader{
    border:1px solid #eceff1;
    margin-bottom: 15px;
  
}
.animated-bg{
        background:linear-gradient(to right,#f3f3f3,#ebebeb);
        animation-duration: 1s;
        animation-fill-mode: forwards;
        animation-iteration-count: infinite;
        animation-name: placeHolderShimmer;
        animation-timing-function: linear;
    }

    .btn-self{
        color: #fff;
        background-color: #4b6dc6;
        border-color: #809bf9;
        &:hover{
            color:#fff;
            background-color: #004395;
        }
    }

.result-view{
    text-align:center;
}